@import '../../common/sass/mixins.scss';

.mainWrapper {

  .container {
    @include flexColumnCenter();
    background-color: white;
    padding: 20px 0;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 100%;


    .text {}

    .iconsBlock {
      width: 250px;
      display: flex;
      justify-content: space-around;
      margin: 15px 0 15px 0;

      .icon {
        width: 35px;
        height: 35px;
      }
    }
  }

}

